.btn:disabled { background-color: #f9f9f9; border: solid 1px #ddd; color: #bbb; } 
.btn:focus:active,
.btn:focus,
.btn:active { outline: none; } 
.dropdown-menu { top: 25px; padding: 3px 0; border-radius: 2px; border: 1px solid #bbb; } 
.dropdown-menu > li > a { padding: 9px 12px; cursor: pointer; } 
.dropdown-menu > li > a:hover { background-color: rgba(81, 92, 230, 0.05); color: #333; } 
.calendar-icon { width: 14px; height: 14px; } 
#top { height: 49px; border-bottom: 1px solid #bbb; padding: 16px; font-size: 10px; } 
#lnb { display: none; position: absolute; width: 200px; top: 0px; bottom: 0; border-right: 1px solid #d5d5d5; padding: 12px 10px; background: #fafafa; } 
/* #right { position: absolute; left: 200px; top: 0px; right: 0; bottom: 0; } */
#lnb label { margin-bottom: 0; cursor: pointer; } 
.lnb-new-schedule { padding-bottom: 12px; border-bottom: 1px solid #e5e5e5; } 
.lnb-new-schedule-btn { height: 100%; font-size: 14px; background-color: #ff6618; color: #ffffff; border: 0; border-radius: 25px; padding: 10px 20px; font-weight: bold; } 
.lnb-new-schedule-btn:hover { height: 100%; font-size: 14px; background-color: #e55b15; color: #ffffff; border: 0; border-radius: 25px; padding: 10px 20px; font-weight: bold; } 
.lnb-new-schedule-btn:active { height: 100%; font-size: 14px; background-color: #d95614; color: #ffffff; border: 0; border-radius: 25px; padding: 10px 20px; font-weight: bold; } 
.lnb-calendars > div { padding: 12px 16px; border-bottom: 1px solid #e5e5e5; font-weight: normal; } 
.lnb-calendars-d1 { padding-left: 8px; } 
.lnb-calendars-d1 label { font-weight: normal; } 
.lnb-calendars-item { min-height: 14px; line-height: 14px; display: flex; padding: 8px 0; } 
.lnb-calendars-item button { background-color: #f8f6f9; } 
.lnb-calendars-item input { padding: 5px 15px; border: 1px solid #ddd; border-radius: 50px; width: 100px; margin-right: 5px; } 
.lnb-footer { color: #999; font-size: 11px; position: absolute; bottom: 12px; padding-left: 16px; } 
#menu { padding: 16px; display: flex;justify-content: center; } 
#menu #todayBtn { position:absolute; right: 10px;}

#dropdownMenu-calendarType { padding: 0 8px 0 11px; } 
#calendarTypeName { min-width: 62px; display: inline-block; text-align: left; line-height: 30px; } 
.move-today { padding: 0 16px; line-height: 30px; } 
.move-day { padding: 8px; font-size: 0; } 
#renderRange { padding: 0 12px; font-size: 19px; vertical-align: middle; } 
.dropdown-menu-title .calendar-icon { margin-right: 8px; } 
#calendar { position: absolute; left: 0; right: 0; bottom: 5px; top: 64px; } 
